<template>
	<text>
	{{ text }}
	</text>
</template>

<script>
	import i18n from '../../libs/i18n';

	export default {
		props: {
			textCode: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				text: ''
			}
		},
		mounted() {
			this.getText(this.textCode);
			
			// 监听语言变化
			uni.$on('languageChange', (data) => {
				// 为了防止意外情况，延迟1秒再重新获取语言包
				setTimeout(() => {
					console.log('languageChange--');
					// 重新获取语言包
					this.getText(this.textCode);
				}, 1000);
			});


		},
		methods: {
			getText(textCode){
				this.text = i18n.getAppTextByCode(textCode);
			}
		},
		watch: {
			textCode: function(newVal, oldVal) {
				this.getText(newVal);
			}
		}
	}
</script>

